.experience__container {
  display: grid;
  grid-template-columns: 50% 50%;
  gap: 2rem;
  justify-content: center;
}

.experience__container > div {
  background: var(--color-bg-variant);
  padding: 2.5rem 3rem;
  border-radius: 2rem;
  border: 1px solid transparent;
  transition: var(--transition);
}

.experience__container > div:hover {
  background: transparent;
  border-color: var(--color-primary);
  color: #fff2f2;
  cursor: default;
}

.experience__container > div > h5 {
  transition: var(--transition);
}

.experience__container > div:hover h5 {
  transition: var(--transition);
  color: #fff2f2;
}

.experience__container > div h5 {
  text-align: center;
  margin-bottom: 2rem;
  color: var(--color-primary);
}

.experience__content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 2rem;
  column-gap: 6rem;
}

.experience__details {
  gap: 2rem;
  text-align: center;
}

svg {
  display: inline;
}

.experience__details-icon {
  margin-top: 6px;
  color: var(--color-primary);
  font-size: 2rem;
}

.experience__skills {
  padding-top: 0.4rem;
}

.iconSpace {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
}

