header {
  height: 90vh;
  overflow: hidden;
}

.header__container {
  display: flex;
  justify-content: center;
  text-align: center;
  height: 100%;
  /* width: 90%; */
  position: relative;
}

.texte_header {
  margin: auto;
}

/* ========== CTA ========== */

.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

/* ========== HEADER SOCIALS ========== */

.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
  font-size: 1.2rem;
}

.header__socials > a {
  color: #fff2f2;
  transition: var(--transition);
}
.header__socials > a:hover {
  color: var(--color-primary);
}

.header__socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background: #fff2f2;
}
/* ========== ME ========== */

.me {
  background: linear-gradient(var(--color-primary), transparent 50%);
  width: 18rem;
  height: 18rem;
  margin: auto;
  left: calc(50% - 9rem);
  border-radius: 40%;
  overflow: hidden;
  padding: 5rem 1, 5rem 1.5rem;
}

/* ========== SCROLL DOWN ========== */

.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 100;
  font-size: 0.9rem;
  color: var(--color-bg);
  transition: var(--transition);
}

.scroll__down:hover {
  color: var(--color-primary);
}

/* ========== MEDIA QUERIES (MEDIUM DEVICES)===========*/
@media screen and (max-width: 1024px) {
  header {
    height: 90vh;
  }
}

@media screen and (max-width: 850px) {
  header {
    height: 80vh;
  }

  .header__socials,
  .scroll__down {
    display: none;
  }

  .header__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 0rem;
  }

  .me {
    width: 14rem;
    height: 14rem;
    margin-top: 2%;
  }
}

/* ========== MEDIA QUERIES (SMALL DEVICES)===========*/
@media screen and (max-width: 600px) {
  header {
    min-height: 70vh;
  }

  .header__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 12%;
  }

  .texte_header {
    margin-top: 0%;
    margin-bottom: 0%;
  }

  .me {
    margin-top: 1%;
    margin-bottom: 0%;
  }
}
